.discord-link {
    position: fixed;
    bottom: 24px;
    left: 24px;
    opacity: 0.3;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}
.discord-link:hover {
    opacity: 1;
}
.discord-link__svg {
    height: 40px;
    fill: #7289DA;
}

.quake-logo {
    position: fixed;
    bottom: 24px;
    right: 36px;
    height: 120px;
    opacity: 0.5;

    -webkit-animation-duration: 36s;

            animation-duration: 36s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-name: quakeColors;
            animation-name: quakeColors;
}

@media (max-width: 575px) {
    .quake-logo {
        bottom: 12px;
        right: 18px;
        height: 60px;
    }
}

.no-underline {
    text-decoration: none !important;
}

@-webkit-keyframes quakeColors {
    0% {
        fill: #D4145A;
    }
    11% {
        fill: #FBB03B;
    }
    22% {
        fill: #009245;
    }
    33% {
        fill: #FCEE21;
    }
    45% {
        fill: #2E3192;
    }
    56% {
        fill: #1bffff;
    }
    67% {
        fill: #662D8C;
    }
    78% {
        fill:  #ED1E79;
    }
    100% {
        fill: #D4145A;;
    }
}

@keyframes quakeColors {
    0% {
        fill: #D4145A;
    }
    11% {
        fill: #FBB03B;
    }
    22% {
        fill: #009245;
    }
    33% {
        fill: #FCEE21;
    }
    45% {
        fill: #2E3192;
    }
    56% {
        fill: #1bffff;
    }
    67% {
        fill: #662D8C;
    }
    78% {
        fill:  #ED1E79;
    }
    100% {
        fill: #D4145A;;
    }
}

.navbar--carnage {
    background: -webkit-linear-gradient(45deg, #FCA5F1, #B5FFFF);
    background: -o-linear-gradient(45deg, #FCA5F1, #B5FFFF);
    background: linear-gradient(45deg, #FCA5F1, #B5FFFF);
}


